.row {
  height: 10vw;
  max-height: 100px;
}

#calculator {
  max-width: 500px;
}

button {
  font-size: min(4vw, 40px);
}

#screen {
  background-color: black;
  border: 8px solid silver;
  color: white;
  text-align: right;
  padding: 2% 5%;
}

#all, #display {
  font-family: 'Orbitron', sans-serif;
  overflow-wrap: break-word;
  line-height: 85%;
}

#all {
  font-size: min(2vw, 20px);
  min-height: 6vw;
}
#display {
  min-height: 6vw;
  font-size: min(4vw, 40px);
}